/* .coffee-card  */

.coffee-card {
margin: 15px 15px;
padding: 15px 15px;
box-shadow: 0 4px 8px 0 rgba(171, 33, 33, 0.2);
transition: 0.3s;
border: solid grey;
border-radius: 15px;
max-width: 30%;
background-color: rgba(208, 94, 94, 0.772);
}

.coffee-card:hover {
  
    box-shadow: 0 8px 16px 0 rgba(114, 6, 6, 0.376);
  }


